@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);

* {
  font-family: 'Montserrat', sans-serif;
  background-color: #2e2d2c;
  color: white;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  text-align: center;
}

.coin-input {
  padding-left: 15px;
  width: 300px;
  height: 30px;
  background: white;
  color: black;
  text-align: left;
}
 .coin-container {
     display: flex;
     justify-content: center;
 }

 .coin-row
 {
     display: flex;
     flex-direction: row;
     justify-items: start;
     align-items: center;
     height: 80px;
     border-bottom: 1px solid #d7d7d7;
     padding: 0 85px 0 5px;
     width: 900px;
 }

 .coin {
     display: flex;
     align-items: center;
     padding-right: 20px;
 }

 .coin-row:hover {
    outline: 8px ridge rgba(170, 50, 220, .6);
    border-radius: 2rem;
    margin-bottom: 12px;
    /* transition-delay: .1s; */
 }

 .coin img {
     height: 30px;
     width: 30px;
     margin-right: 20px;
 }

 .coin h1 {
     font-size: 18px;
     width: 150px;
 }

 .coin-symbol {
     text-transform: uppercase;
     width: 50px;
 }

 .coin-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
 }

.coin-price {
    width: 180px;
}

.coin-volume {
    width: 180px;
}

.coin-marketcap {
    width: 300px; 
}

.coin-percent {
    width: 80px;
}

.red {
    color: red;
}

.green {
    color: green;
}


